import React, { Component, PropTypes } from 'react';
import { connect } from 'react-redux';
import Helmet from 'react-helmet';
import ContentCard from './ContentCard';
import Header from './Header';
import Sidebar from './Sidebar';
import Login from './Login';

// for rou
import {BrowserRouter as Router, Route} from 'react-router-dom'
class App extends Component {
  constructor(){
    super();
    this.state={
      showLayoutOf4:false,
      showLayoutOf3:false,
      likeSort:false,
      trendSort:false
    }
   
  }

  // layout selectors
  toggleLayout3(){
    this.setState({
      showLayoutOf3:true,
      showLayoutOf4:false
    })
  }
  toggleLayout4(){
    this.setState({
      showLayoutOf3:false,
      showLayoutOf4:true
    })
  }

  // content sorting options
  toggleLikeSort(){
    this.setState({likeSort:!this.state.likeSort})

  }

  toggleTrendSort(){
    this.setState({trendSort:!this.state.trendSort})
  }



  render() {
    
    return (
      <div id="row">
        <Header layout3={this.toggleLayout3.bind(this)} layout4 = {this.toggleLayout4.bind(this)} likesort={this.toggleLikeSort.bind(this)} trendsort={this.toggleTrendSort.bind(this)} />
        <div className="container-fluid content no_padding">
          <div className="col-sm-2 no_padding">
            <Sidebar/>
          </div>
          <div className="col-xs-10 no_padding">
            <div className="row no_margin">
              <ContentCard layout3={this.state.showLayoutOf3} layout4={this.state.showLayoutOf4} like={this.state.likeSort} trend={this.state.trendSort}/>
            </div>
          </div>

        </div>
        
      </div>
    )
  }
}

function mapStateToProps(state) {
  return {}
}

export default connect(mapStateToProps)(App)
